* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html,
body {
  width: 100%;
  height: 100%;
}
.inp {
  margin: 10px;
  border-radius: 10px;
  border: none;
  padding: 2px 5px;
  outline: none;
}
.logo {
  width: 35px;
  height: 35px;
}
.box2 {
  padding: 10px;
  background-color: #2c3e50;
  color: #fff;
  display: inline-block;
  margin: 20px;
  width: 310px;
  height: auto;
  border-radius: 3px;
}
.box2 p {
  width: 95%;
  word-break: keep-all;
}
.main {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.img {
  width: 99%;
  height: 300px;
  margin: 10px auto 10px auto;
  background-size: cover;
}
.loader {
  width: 100%;
  height: calc(100vh - 80px);
  display: flex;
  align-items: center;
  justify-content: center;
}
.pag {
  display: flex;
  justify-content: center;
  padding: 20px 0;
}
.pag Button {
  margin: 10px;
  background-color: #2c3e50;
}
.line {
  width: 90%;
  margin: auto;
  height: 1px;
  background-color: #2c3e50;
  border-radius: 50%;
}
.mobilenav {
  display: flex;
  align-items: center;
  height: 60px;
  width: 100%;
  justify-content: space-between;
  padding: 0 10px;
  background: rgb(33, 37, 41);
  color: #fff;
  display: none;
}
.mobilenav img {
  width: 30px;
  height: 30px;
  margin: 0 10px;
}
.mobilenav h1 {
  font-size: 20px;
}
.mobilenav input {
  width: 175px;
}
.select {
  display: none;
}
@media (max-width: 800px) {
  .navbar {
    display: none;
    overflow: hidden;
  }
  .mobilenav {
    display: flex;
  }
  .select {
    display: block;
    padding: 20px;
    display: flex;
    justify-content: flex-start;
    width: 100%;
  }
}
